.App {
  text-align: center;
}

.black-nav {
  background-color: black;
  color: white;
  padding: 4px 0;
}

.content {
  display: flex;
  align-items: center;
  height: 32px;
  box-shadow: 0 0.5px 0 0 rgba(0, 0, 0, 0.5);
}

.content span {
  display: inline-block;
}

.content .index {
  width: 60px;
  font-size: 12px;
}

.content .tag {
  width: 80px;
  font-size: 14px;
  color: gray;
}

.content .title {
  flex-grow: 1;
  text-align: left;
  text-indent: 10px;
  font-size: 16px;
  cursor: pointer;
}

.content .created {
  width: 80px;
}

.content .hit {
  width: 60px;
  font-size: 14px;
}

.content .like {
  width: 60px;
  font-size: 14px;
}

.content .icon {
  width: 30px;
  cursor: pointer;
}


.modal {
  background-color: gainsboro;
  text-align: left;
  text-indent: 15px;
}